var processResponse = function processResponse(result, successMessage) {
  if (!result.success) {
    processAjaxError(result);
  } else if (successMessage) {
    console.log(successMessage);
    openNotification({
      title: i18n.t('admin.notification.title.success'),
      message: successMessage,
      type: 'success',
      duration: 4500
    });
  } else if (result.i18n_message) {
    openNotification({
      title: i18n.t('admin.notification.title.success'),
      message: i18n.t(result.i18n_message, result.i18n_message_object),
      type: 'success',
      duration: 4500
    });
  }

  return Promise.resolve(result);
};

var post = function post(uri, params, successMessage) {
  return axios.post(uri, params).then(function (response) {
    return processResponse(response.data, successMessage);
  }).catch(function (e) {
    openNotification({
      title: i18n.t('admin.notification.title.failed'),
      message: i18n.t('admin.notification.message.unknownError'),
      type: 'danger',
      duration: 4500
    });
    return Promise.resolve({ success: false });
  });
};

